<template>
    <div class="login-container">
        <p class="login-title">관리자 정보입력</p>
        <form novalidate @submit.prevent="validate">
            <div class="w-full">
                <v-text-field style="border-radius:5px;"
                              v-model="uid"
                              :error-messages="uidErrors"
                              outlined
                              label="아이디"
                              @input="$v.uid.$touch()"
                              @blur="$v.uid.$touch()"
                              placeholder="아이디를 입력해주세요"/>
                <v-text-field type="password" style="border-radius:5px;"
                              outlined
                              label="비밀번호"
                              :error-messages="upassErrors"
                              @input="$v.upass.$touch()"
                              @blur="$v.upass.$touch()"
                              v-model="upass"
                              placeholder="비밀번호를 입력해주세요"/>
                <v-text-field type="password" style="border-radius:5px;"
                              outlined
                              label="비밀번호 확인"
                              :error-messages="reUpassErrors"
                              @input="$v.reUpass.$touch()"
                              @blur="$v.reUpass.$touch()"
                              v-model="reUpass"
                              placeholder="비밀번호를 다시 입력해주세요"/>

                <v-text-field style="border-radius:5px;"
                              v-model="name"
                              :error-messages="nameErrors"
                              outlined
                              label="이름"
                              @input="$v.name.$touch()"
                              @blur="$v.name.$touch()"
                              placeholder="이름을 입력해주세요"/>

                <v-text-field style="border-radius:5px;"
                              v-model="email"
                              :error-messages="emailErrors"
                              outlined
                              label="이메일"
                              @input="$v.email.$touch()"
                              @blur="$v.email.$touch()"
                              placeholder="이메일을 입력해주세요"/>

            </div>
            <div class="w-full flex flex-row wrap-checkbox justify-between my-1 px-2"
                 style="margin-top:-20px;padding-bottom:15px;">
                <div class="flex flex-row items-center">
                    <div style="padding-left:10px;">
                    </div>
                </div>
            </div>
            <div class="button-row">
                <v-btn type="submit" style="background: #000;color:#fff;height:47px;border-radius: 5px;width:100%;">
                    <span>관리자 정보 저장</span>
                </v-btn>
            </div>
        </form>
    </div>
</template>
<script>
import {validationMixin} from 'vuelidate'
import {required,email} from 'vuelidate/lib/validators'
import apiService from '@/config/admin/apiService'
import {
    API_ADMIN_INSERT_ADMIN_INFO
} from '@/config/admin/urls'


export default {
    mixins: [validationMixin],
    validations: {
        uid: {required},
        upass: {required},
        reUpass:{required},
        name:{required},
        email:{required,email}
    },
    name: 'AdminInsert',
    computed: {
        uidErrors() {
            const errors = []
            if (!this.$v.uid.$dirty) return errors
            !this.$v.uid.required && errors.push('아이디를 입력하세요')
            return errors;
        },
        upassErrors() {
            const errors = []
            if (!this.$v.upass.$dirty) return errors
            !this.$v.upass.required && errors.push('비밀번호를 입력하세요')
            return errors;
        },
        reUpassErrors() {
            const errors = []
            if (!this.$v.reUpass.$dirty) return errors
            !this.$v.reUpass.required && errors.push('비밀번호를 다시 입력하세요')
            if(this.reUpass && this.reUpass != this.upass) {
                errors.push('비밀번호와 비밀번호확인이 다릅니다')
            }
            return errors;
        },
        nameErrors() {
            const errors = []
            if (!this.$v.name.$dirty) return errors
            !this.$v.name.required && errors.push('관리자명을 입력하세요')
            return errors;
        },
        emailErrors() {
            const errors = []
            if (!this.$v.email.$dirty) return errors
            !this.$v.email.required && errors.push('이메일을 입력하세요')
            !this.$v.email.email && errors.push('잘못된 이메일 형식입니다')
            return errors;
        },
    },
    data() {
        return {
            uid: '',
            upass: '',
            reUpass:'',
            name:'',
            email:'',
        };
    },
    created() {

    },
    methods: {
        validate() {
            this.$v.$touch();
            if (!this.$v.$anyError) {
                const frm = {
                    uid: this.uid,
                    upass: this.upass,
                    name:this.name,
                    email:this.email
                }
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_INSERT_ADMIN_INFO, frm, (res) => {

                    this.$eventBus.$emit('overlay', 'hide');
                    let result = res.status;
                    let params = {}
                    if (result == 'success' && res.access_token) {
                        sessionStorage.setItem('accessToken',res.access_token)
                        apiService.accessToken = res.access_token;
                        params = {
                            message: '관리자 정보가 저장 되었습니다',
                            doAction: () => {
                                this.$emit('insertResult',true);
                            }
                        };

                    } else {
                        let msg = '';
                        switch (result) {
                            case 'doubleUid':
                                msg = '중복된 아이디 입니다'
                                break;
                            case 'doubleEmail':
                                msg = '중복된 이메일 입니다'
                                break;
                            default:
                                msg = '잘못된 접근입니다'
                                break;
                        }
                        params = {
                            message: msg,
                        };

                    }
                    this.$eventBus.$emit('modalOpen', params);

                })
            }
        },
    }

}
</script>

<style lang="scss" scoped>
.login-container {
    width: 400px;
    margin:100px auto;
    .login-title {
        text-align: center;
        color: #000;
        font-size: 22pt;
        font-weight: bold;
        margin-bottom:50px;
    }
    .button-row {
        width:250px;
        margin:auto;
    }
}
</style>