<template>
    <div>
        <LAYOUT v-if="show.login"></LAYOUT>
        <ADMININSERT v-else-if="show.adminInsert" @insertResult="insertActive"></ADMININSERT>
    </div>
</template>
<script>
import LAYOUT from './layout/layout'
import LOGIN from './login/login'
import ADMININSERT from './login/adminInsert'
import apiService from '@/config/admin/apiService'
import {
    API_ADMIN_GET_LOGIN_CHECK
} from '@/config/admin/urls'
import {mapActions} from "vuex";
export default {
    components: {
      LAYOUT,
      ADMININSERT
    },
    data() {
        return {
            show:{
                login:false,
                logout:true,
                adminInsert:false
            },
        };
    },
    created() {
        this.checkStatus();


    },
    methods: {
        ...mapActions('admin', ["setSiteEnv"]),
        checkStatus() {
            apiService.post(API_ADMIN_GET_LOGIN_CHECK, {}, (res) => {
                if(res.status=='success') {
                    this.show.logout = false;
                    this.show.login = true;
                } else {
                    apiService.accessToken = null;
                    sessionStorage.setItem('accessToken','');
                    if(res.data=='emptyAdmin') {
                        this.show.adminInsert = true;
                        this.show.logout = false;
                    } else {
                        location.href = '/';
                    }
                }
            },(error) =>{
                switch (error.response.status) {
                    case 500:
                        this.accessToken = null;
                        sessionStorage.setItem('accessToken', '');
                        location.href = '/';
                        break;
                }
            })
        },
        loginActive(result) {
            this.show.login = result;
            if(this.show.login) {
                this.show.logout = false;
            } else {
                this.show.logout = true;
            }
        },
        insertActive() {
            this.show.login = true;
            this.show.adminInsert = false;
            this.show.logout = false;

        }
    }

}
</script>

<style>
.submit-button {
    background:#2196F3 !important;
    color:#fff !important;
    width:170px;
    height:45px !important;
}



</style>