<template>
    <div class="login-container">
        <p class="login-title">ADMIN LOGIN</p>
        <form novalidate @submit.prevent="validate">
            <div class="w-full">
                <v-text-field style="border-radius:5px;"
                              v-model="uid"
                              :error-messages="uidErrors"
                              outlined
                              label="아이디"
                              @input="$v.uid.$touch()"
                              @blur="$v.uid.$touch()"
                              placeholder="아이디를 입력해주세요"/>
                <v-text-field type="password" style="border-radius:5px;"
                              outlined
                              label="비밀번호"
                              :error-messages="upassErrors"
                              @input="$v.upass.$touch()"
                              @blur="$v.upass.$touch()"
                              v-model="upass"
                              placeholder="비밀번호를 입력해주세요"/>
            </div>
            <div class="w-full flex flex-row wrap-checkbox justify-between my-1 px-2"
                 style="margin-top:-20px;padding-bottom:15px;">
                <div class="flex flex-row items-center">
                    <div style="padding-left:10px;">
                    </div>
                </div>
            </div>
            <div class="button-row">
                <v-btn type="submit" style="background: #000;color:#fff;height:47px;border-radius: 5px;width:100%;">
                    로그인
                </v-btn>
            </div>
        </form>
    </div>
</template>
<script>
import {validationMixin} from 'vuelidate'
import {required} from 'vuelidate/lib/validators'
import apiService from '@/config/admin/apiService'
import {
    API_ADMIN_URL_LOGIN
} from '@/config/admin/urls'


export default {
    mixins: [validationMixin],
    validations: {
        uid: {required},
        upass: {required},
    },
    name: 'AdminLogin',
    computed: {
        uidErrors() {
            const errors = []
            if (!this.$v.uid.$dirty) return errors
            !this.$v.uid.required && errors.push('아이디를 입력하세요')
            return errors;
        },
        upassErrors() {
            const errors = []
            if (!this.$v.upass.$dirty) return errors
            !this.$v.upass.required && errors.push('비밀번호를 입력하세요')
            return errors;
        }
    },
    data() {
        return {
            uid: '',
            upass: '',
        };
    },
    created() {

    },
    methods: {
        validate() {
            this.$v.$touch();
            if (!this.$v.$anyError) {
                const frm = {
                    uid: this.uid,
                    upass: this.upass
                }
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_URL_LOGIN, frm, (res) => {

                    this.$eventBus.$emit('overlay', 'hide');
                    let result = res.status;
                    let params = {}
                    if (result == 'success' && res.access_token) {
                        sessionStorage.setItem('adminAccessToken',res.access_token)
                        apiService.accessToken = res.access_token;
                        params = {
                            message: '로그인 되었습니다',
                            doAction: () => {
                                this.$emit('loginResult',true);
                            }
                        };

                    } else {
                        params = {
                            message: '잘못된 아이디 또는 비밀번호입니다',
                        };

                    }
                    this.$eventBus.$emit('modalOpen', params);

                })
            }
        },
    }

}
</script>

<style lang="scss" scoped>
.login-container {
    width: 400px;
    margin: 200px auto;
    .login-title {
        text-align: center;
        color: #000;
        font-size: 22pt;
        font-weight: bold;
        margin-bottom:50px;
    }
    .button-row {
        width:250px;
        margin:auto;
    }
}
</style>