<template>
    <div class="layout-page" id="site-base-layout">


        <div class="layout-header">
            <div class="top-box">
                <div class="top-info">
                    <span @click="goPage('/')">사용자 페이지</span>
                </div>

                <div class="top-setting">
                    <ul class="top-setting-ul">
                        <li @click="goAdminUpdate()">관리자 정보수정</li>
                        <li @click="logout()">로그아웃</li>
                    </ul>
                </div>
            </div>

        </div>
        <div class="layout-container">
            <div class="left-menu">
                <div class="company-info" @click="goPage('/admin')">
                    <span class="flow">KMS</span> <span class="shop">Center</span>
                </div>
                <vue-custom-scrollbar class="scroll-area">
                    <ul class="large-menu">
                        <li class="large-menu-item" v-for="(item,index) in menuList" :class="item.active">
                            <div class="large-menu-box" @click="showMenu(index)">
                                <font-awesome-icon class="menu-icon" :icon="item.icon"/>
                                <span class="large-menu-item-title">{{ item.name }}</span>
                                <font-awesome-icon class="icon" v-if="item.active" icon="fa-solid fa-angle-up"/>
                                <font-awesome-icon class="icon" v-else icon="fa-solid fa-angle-down"/>
                            </div>
                            <ul class="submenu-ul">
                                <li v-for="(subItem,subIndex) in item.subMenuList" @click="goSubMenu(index,subIndex)"
                                    :class="subItem.active">
                                    <font-awesome-icon class="sub-icon" icon="fa-solid fa-square"/>
                                    <span>{{ subItem.name }}</span>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </vue-custom-scrollbar>
            </div>
            <div class="layout-contents">
                <div class="submenu-title" v-if="subMenuTitle">{{ subMenuTitle }}</div>
                <router-view/>
            </div>
        </div>
        <div class="layout-bottom"></div>
    </div>
</template>
<script>

import {mapGetters, mapActions} from "vuex";
import {ADMIN_MENU} from '@/config/menu/adminMenu'
import apiService from "@/config/admin/apiService";
import {
    API_ADMIN_URL_LOGOUT,
    API_ADMIN_URL_UPDATE_SITEXML
} from "@/config/admin/urls";
import JQ from 'jquery'
import vueCustomScrollbar from 'vue-custom-scrollbar'
import "vue-custom-scrollbar/dist/vueScrollbar.css"

export default {
    components: {
        vueCustomScrollbar,
    },
    computed: {
        ...mapGetters('admin', ["getPageMenuName", "getSiteEnv"]),
    },
    watch: {
        '$route'(to, from) {
            switch (to.name) {
                case 'adminProductProductModify':
                    this.subMenuTitle = '상품 수정';
                    break;
                case 'adminProductProductRegist':
                    if (from.name == 'adminProductProductModify') {
                        this.$eventBus.$emit('productRegistReload', {});
                    }
                    break;
                case 'adminMain':
                case 'adminUpdate':
                    this.subMenuTitle = '';
                    let k = 0;
                    for (let menus of this.menuList) {
                        if (menus.active) {
                            JQ('.submenu-ul').eq(k).slideUp(200);
                        }
                        menus.active = '';
                        for (let menu of menus.subMenuList) {
                            menu.active = '';
                        }
                        k++;
                    }

                    break;
            }

        },
        getSiteEnv(val) {
            this.siteEnv = val;

        },
        getPageMenuName(val) {
            if (val) {
                this.subMenuTitle = val;
                this.setPageMenuName('');
            }
        }
    },
    data() {
        return {
            menuList: [],
            currentUrl: '',
            subMenuTitle: '',
            activeIndex: 0,
            activeFlag: false,
            siteEnv: '',
        };
    },
    created() {
        this.$eventBus.$on('goPage', this.goPageFromMain)
        this.currentUrl = this.$route.path;
        if (!this.getSiteEnv) {
            setTimeout(() => {
                this.siteEnv = this.getSiteEnv;
            }, 200)
        } else {
            this.siteEnv = this.getSiteEnv;
        }
        this.setMenu();
    },
    methods: {
        ...mapActions('admin', ["setPageMenuName"]),

        setMenu() {
            this.menuList = [];
            for (const menu of ADMIN_MENU) {
                this.menuList.push(menu);
            }

            let index = 0;
            for (let menus of this.menuList) {
                for (let menu of menus.subMenuList) {
                    if (this.currentUrl == menu.url) {
                        menus.active = 'active';
                        menu.active = 'active';
                        this.activeFlag = true;
                        this.activeIndex = index;
                        this.subMenuTitle = menu.name;
                    }
                }
                index++;
            }

        },
        goAdminUpdate() {
            this.$router.push('/admin/adminUpdate')
        },
        showMenu(index) {

            this.goSubMenu(index, 0);
        },
        goSubMenu(index, subIndex) {

            let k = 0;
            for (let menus of this.menuList) {
                menus.active = '';
                if (index != k) {
                    JQ('.submenu-ul').eq(k).slideUp(200);
                }
                k++;
                for (let menu of menus.subMenuList) {
                    menu.active = '';
                }
            }
            JQ('.submenu-ul').eq(index).slideDown(200);

            this.menuList[index].active = 'active';
            this.menuList[index].subMenuList[subIndex].active = 'active';
            this.subMenuTitle = this.menuList[index].subMenuList[subIndex].name;
            const url = this.menuList[index].subMenuList[subIndex].url;


            if (url) {
                //if (this.currentUrl != url) {
                //  this.currentUrl = url;
                this.goPage(url);
                //}
            }

        },
        goPageFromMain(params) {
            this.currentUrl = params.url;
            this.setMenu()
        },
        goPage(url) {
            this.$router.push(url).catch(() => {
            });
        },
        logout() {
            this.$eventBus.$emit('overlay', 'open');
            apiService.post(API_ADMIN_URL_LOGOUT, {}, (res) => {
                apiService.accessToken = null;
                sessionStorage.setItem('accessToken','');
                location.href = '/';
            })
        }

    },
    mounted() {
        if (this.activeFlag) {
            this.activeFlag = false
            JQ('.submenu-ul').eq(this.activeIndex).slideDown(200);
        }
    },
    updated() {
        if (this.activeFlag) {
            this.activeFlag = false
            JQ('.submenu-ul').eq(this.activeIndex).slideDown(200);
        }
    },
    destroyed() {
        this.$eventBus.$off('goPage')
    }

}
</script>

<style lang="scss" scoped>
.layout-page {
    width: 100%;

    .scroll-area {
        height: calc(100vh - 60px);
    }

    .layout-header {
        width: 100%;
        height: 60px;
        border-bottom: solid 1px #ccc;

        .top-box {
            height: 60px;
            display: flex;
            justify-content: space-between;

            .top-info {
                margin-left: 250px;
                width: 80%;
                padding:30px 0 0 60px;
                span {
                    text-decoration: underline;
                    font-weight: bold;
                    cursor: pointer;
                    font-size:14px;
                    color:#B71C1C;
                }
            }

            .top-setting {
                min-width: 300px;
                padding: 30px 20px 0 0;

                .top-setting-ul {
                    display: flex;
                    justify-content: flex-end;

                    li {
                        cursor: pointer;
                        text-decoration: underline;
                        font-size: 13px;
                        margin-right: 20px;
                    }
                }
            }
        }
    }

    .layout-container {
        .left-menu {
            width: 250px;
            top: 0;
            position: fixed;
            min-width: 250px;
            background: #222;
            min-height: calc(100vh);
            height: auto;
            text-align: left;

            .company-info {
                cursor: pointer;
                width: 100%;
                height: 59px;
                font-weight: bold;
                line-height: 59px;
                font-size: 22px;
                padding-left: 20px;
                background: #fff;

                span {
                    font-weight: bold;
                    font-size: 22px;

                    &.flow {
                        color: #2196F3;
                    }

                    &.shop {
                        font-size: 24px;
                        color: #C51162;
                    }
                }
            }

            .large-menu {
                width: 100%;
                margin: 0;
                padding: 20px 20px 30px 20px;

                li.large-menu-item {
                    color: #fff;
                    position: relative;
                    padding-bottom: 30px;

                    span.large-menu-item-title {
                        padding-left: 10px;
                        font-weight: bold;
                        font-size: 17px;
                    }

                    cursor: pointer;

                    .menu-icon {
                        font-size: 15px;
                    }

                    .icon {
                        font-size: 17px;
                        position: absolute;
                        right: 0;
                        top: 4px;
                    }

                    &.active {
                        span.large-menu-item-title {
                            color: #4FC3F7;
                            font-weight: bold;
                        }

                        .menu-icon {
                            color: #4FC3F7;
                        }
                    }

                    .submenu-ul {
                        display: none;
                        margin: 10px 0 0 0;
                        border-top: solid 1px #777;
                        padding: 10px 0 0 10px;

                        li {
                            color: #fafafa;
                            margin: 0;
                            padding: 0 0 10px 0;
                            cursor: pointer;
                            display: flex;

                            span {
                                font-size: 14px;
                                font-weight: normal;
                                display: inline-block;
                                width: 190px;
                            }

                            .sub-icon {
                                margin: 6px 10px 0 0;
                                font-size: 8px;
                            }

                            &.active {
                                span {
                                    color: #4FC3F7;
                                    font-weight: normal;
                                }

                                .sub-icon {
                                    color: #4FC3F7;
                                }

                            }
                        }
                    }
                }
            }
        }

        .layout-contents {
            border-left: solid 1px #ccc;
            margin: 0 0 0 250px;
            width: calc(100vw - 260px);
            height: auto;
            min-height: calc(100vh - 130px);
            background: #e8e8e8;
            padding: 20px 20px 50px 50px;

            .submenu-title {
                font-weight: bold;
                color: #000;
                font-size: 15pt;
                padding-left: 10px;
                padding-bottom: 20px;
            }

        }
    }

    .layout-bottom {
        background: #e8e8e8;
        width: 100%;
        height: 70px;
    }

}
</style>
